.container-main {
  background-image: url(/static/media/background1.6157086d.png);
  background-size: cover;
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: right;
}

.container-home {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  color: white;
  margin-left: 5%;
}

#container-div-main {
  display: flex;
  flex-direction: column;
  margin-top: 300px;
}

#text1 {
  font-size: 27px;
  padding: 3px;
}

#text2 {
  font-size: 40px;
  padding: 3px;
}

#text3 {
  font-size: 20px;
  padding: 3px;
}

.div-container-redirects {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  padding: 3px;
}

#btn-works {
  border-radius: 40px;
  background-color: rgb(3, 219, 94);
  color: white;
  margin-left: 10px;
  font-weight: 550;
  padding: 12px 20px;
}

#btn-works:hover {
  background-color: rgb(15, 138, 66);
  color: rgb(3, 219, 94);
  box-shadow: none;
}

#btn-aboult {
  border-radius: 40px;
  background-color: white;
  color: rgb(3, 219, 94);
  margin-left: 10px;
  font-weight: 550;
  padding: 12px 20px;
}

#btn-aboult:hover {
  background-color: rgb(3, 219, 94);
  color: white;
  box-shadow: none;
}

#btn-contacts {
  border-radius: 40px;
  background-color: rgb(39, 49, 89);
  color: rgb(3, 219, 94);
  margin-left: 10px;
  border: rgb(3, 219, 94);
  font-weight: 550;
  padding: 12px 20px;
}

#btn-contacts:hover {
  background-color: rgb(3, 219, 94);
  color: rgb(39, 49, 89);
  box-shadow: none;
}

.links-conteudo {
  background-color: rgb(226, 222, 222);
  border-radius: 30px;
  border: rgb(226, 222, 222) solid 2px;
  box-shadow: 6px 6px 4px rgb(206, 203, 203);
  margin: 30px;
}

.links-conteudo:hover {
  background-color: rgba(206, 203, 203, 0.75);
  box-shadow: none;
}

.links-conteudo h2 {
  font-size: 20px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  color: rgb(0, 0, 0);
  text-shadow: rgb(188, 182, 182) 0.1em 0.1em 0.2em;
  text-decoration: none;
}

@media (min-width: 769px) and (max-width: 1024px) {
  .container-home {
    margin-left: 3%;
  }

  #text1 {
    font-size: 23px;
  }

  #text2 {
    font-size: 35px;
  }

  #text3 {
    font-size: 17px;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .container-home {
    margin-left: 3%;
  }

  #container-div-main {
    margin-top: 280px;
  }

  #text1 {
    font-size: 18px;
  }

  #text2 {
    font-size: 28px;
  }

  #text3 {
    font-size: 14px;
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  .container-home {
    margin-left: 1%;
  }

  #container-div-main {
    margin-top: 50px;
  }

  #text1 {
    font-size: 13px;
  }

  #text2 {
    font-size: 20px;
  }

  #text3 {
    font-size: 14px;
  }
}
